import React, { useState } from "react";

import { name } from "../../package.json";

const Search = ({ customClass }) => (
  <input
    type="text"
    placeholder="search items here..."
    className={`transition-all rounded-full bg-purple-200 text-gray-700 placeholder-gray-500 text-center md:text-left ${customClass}`}
  />
);

const Navbar = () => {
  const [search, setSearch] = useState(false);
  const [menu, setMenu] = useState(false);

  return (
    <nav className="text-gray-700 h-20 py-2 px-3 w-screen mb-5">
      <div className="max-w-7xl mx-auto lg:flex justify-between">
        <div className="flex justify-between items-center space-x-2">
          <button className="flex items-center px-2 py-2 z-10">
            <svg
              className="h-12 w-12 mr-1 text-purple-700"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M20 7l-8-4-8 4m16 0l-8 4m8-4v10l-8 4m0-10L4 7m8 4v10M4 7v10l8 4"
              />
            </svg>
            <span className="text-3xl font-semibold">{name}</span>
          </button>
          <div className="h-11 flex items-center space-x-2 z-10">
            <div className="hidden sm:flex items-center relative">
              <Search
                customClass={
                  (search ? "w-52 pl-5 pr-12" : "mr-6 px-0 w-0") +
                  " h-10 focus:w-72 lg:w-52 lg:px-5"
                }
              />
              <button
                className="lg:hidden h-11 w-11 bg-purple-700 text-gray-200 rounded-full active:bg-purple-900 absolute right-0"
                onClick={() => setSearch(!search)}
              >
                <svg
                  className="h-11 w-6 mx-auto"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path
                    fillRule="evenodd"
                    d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
                    clipRule="evenodd"
                  />
                </svg>
              </button>
            </div>
            <button
              className="lg:hidden h-11 w-11 bg-purple-700 text-gray-200 rounded-full active:bg-purple-900"
              onClick={() => setMenu(!menu)}
            >
              <svg
                className="h-11 w-6 mx-auto"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
          </div>
        </div>

        <div
          className={
            (menu ? "top-0" : " -top-full") +
            " flex flex-col bg-purple-100 items-center transition-all space-y-4 h-full w-full pt-20 absolute right-0 z-0 lg:static lg:bg-transparent lg:flex-row lg:transition-none lg:h-auto lg:w-auto lg:py-0 lg:space-y-0 lg:space-x-4 lg:text-lg xl:text-xl"
          }
        >
          <Search customClass="sm:hidden h-8 w-1/2 focus:w-3/4 px-5" />
          <a className="hover:text-purple-700" href="/#">
            <span>Home</span>
          </a>
          <a className="hover:text-purple-700" href="/#">
            <span>Explore</span>
          </a>
          <a className="hover:text-purple-700" href="/#">
            <span>Pages</span>
          </a>
          <a className="hover:text-purple-700" href="/#">
            <span>Stats</span>
          </a>
          <a className="hover:text-purple-700" href="/#">
            <span>Elements</span>
          </a>
          <button className="bg-purple-700 hover:bg-purple-800 active:bg-purple-900 text-gray-100 rounded-2xl px-5 py-1 lg:px-3">
            <span>Connect Wallet</span>
          </button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
